$TitanMain_color: #d41443;
$TitanSecond_color: #ffa5ba;
$SansSecondText_color: #907e82;

// HUMBURGER SITTINGS

$hamburger-padding-x: 0px !default;
$hamburger-padding-y: 0px !default;
$hamburger-layer-width: 20px !default;
$hamburger-layer-height: 3px !default;
$hamburger-layer-spacing: 4px !default;
$hamburger-layer-color: $TitanMain_color !default;
$hamburger-layer-border-radius: 4px !default;
$hamburger-hover-opacity: 0.7 !default;
$hamburger-active-layer-color: $hamburger-layer-color !default;
$hamburger-active-hover-opacity: $hamburger-hover-opacity !default;
$hamburger-hover-use-filter: false !default;
$hamburger-hover-filter: opacity(50%) !default;
$hamburger-active-hover-filter: $hamburger-hover-filter !default;

.adress__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.83em;
  color: black;
  letter-spacing: 0.04em;
}
.flex__centered {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
